<template>
  <v-dialog v-model="modal" width="600px">
    <template v-slot:activator="{ on, attrs }">
      <v-list-item>
        <v-list-item-action>
          <v-checkbox
            @click="switchMarkDone"
            :input-value="task.done"
          ></v-checkbox>
        </v-list-item-action>

        <v-list-item-content v-bind="attrs" v-on="on">
          <v-list-item-title v-text="task.title"></v-list-item-title>
        </v-list-item-content>

        <v-list-item-action class="frow">
          <v-icon v-bind="attrs" v-on="on">mdi-eye-outline </v-icon>

          <v-btn @click="removeTask" color="error" text class="mr-3">
            remove</v-btn
          >
          <v-btn @click="setEdit" color="primary accent-4" class="white--text">
            edit
          </v-btn>
        </v-list-item-action>
      </v-list-item>
    </template>

    <todo-modal-task @cancelModal="onCancelModal" :task="task" />
  </v-dialog>
</template>

<script>
import TodoModalTask from "./TodoModalTask.vue";
export default {
  name: "ToDoItemBody",
  components: { TodoModalTask },

  props: {
    task: Object,
    editing: Boolean,
  },
  data() {
    return {
      modal: false,
    };
  },
  methods: {
    switchMarkDone() {
      this.$emit("switch");
    },
    setEdit() {
      this.$emit("setEdit", true);
    },
    removeTask() {
      this.$emit("removeTask");
    },
    onCancelModal() {
      this.modal = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.frow {
  flex-direction: row;
}
</style>
