<template>
  <v-list-item>
    <form class="d-flex flex-grow-1" @submit.prevent="onSave">
      <v-text-field class="pt-0" v-model="newTaskTitle"></v-text-field>

      <v-list-item-action class="flex-row">
        <v-btn @click="cancelEdit" color="error" text>cancel</v-btn>
        <v-btn type="submit" class="ml-3 white--text" color="primary accent-4"
          >save</v-btn
        >
      </v-list-item-action>
    </form>
  </v-list-item>
</template>

<script>
export default {
  name: "TodoItemForm",
  data() {
    return {
      newTaskTitle: this.title,
    };
  },
  props: {
    title: String,
  },
  methods: {
    cancelEdit() {
      this.$emit("cancelEdit");
    },
    onSave() {
      this.$emit("edit", this.newTaskTitle);
    },
  },
};
</script>

