export default {

  switchMarkDone({ commit, dispatch }, payLoad) {
    commit('switchMarkDone', payLoad)
    dispatch("saveState")

  },
  createTask({ commit, dispatch }, title) {
    const newTask = {
      title: title,
      descr: 'it is description for all tasks ',
      done: false,
      id: Date.now()
    }
    commit('createTask', newTask)
    dispatch("saveState")
  },
  edit({ commit, dispatch }, { title, id }) {
    commit('edit', { title, id })
    dispatch("saveState")

  },
  removeTask({ commit, dispatch }, id) {
    commit("removeTask", id)
    dispatch("saveState")

  },
  saveState({ state }) {
    const tasks = JSON.stringify(state.tasks);
    localStorage.setItem('tasks', tasks);
  },
  loadState({ commit }) {
    const tasks = JSON.parse(localStorage.getItem('tasks'))
    commit("loadState", tasks)

  }

}