<template>
  <div>
    <todo-item-body
      v-if="!editing"
      @switch="switchMarkDone"
      @setEdit="onSetEdit"
      :task="task"
      :editing="editing"
      @removeTask="onRemoveTask"
    ></todo-item-body>

    <todo-item-form
      @edit="onEdit"
      @cancelEdit="onCancelEdit"
      v-else
      :title="task.title"
    />
  </div>
</template>

<script>
import TodoItemBody from "./TodoItemBody";
import TodoItemForm from "./TodoItemForm";
export default {
  name: "TodoItem",
  components: {
    TodoItemBody,
    TodoItemForm,
  },
  data() {
    return {
      editing: false,
    };
  },
  props: {
    task: Object,
  },
  methods: {
    switchMarkDone() {
      this.$emit("switch", this.task.id);
    },
    onSetEdit() {
      this.editing = true;
    },
    onCancelEdit() {
      this.editing = false;
    },
    onEdit(title) {
      this.editing = false;
      this.$emit("edit", { title: title, id: this.task.id });
    },
    onRemoveTask() {
      this.$emit("removeTask", this.task.id);
    },
  },
};
</script>

<style lang="scss" scoped>
</style>
