<template>
  <div>
    <form @submit.prevent="onSubmit" class="d-flex flex-grow-1">
      <v-text-field
        :value="value"
        @input="onInput"
        label="Search"
        single-line
        hide-details
        append-icon="mdi-magnify"
        clearable
      ></v-text-field>
      <!-- <input
        :value="value"
        @input="$emit('input', $event.target.value)"
        label="Search"
        single-line
        hide-details
        append-icon="mdi-magnify"
      /> -->
    </form>
  </div>
</template>

<script>
export default {
  name: "TodoSearch",
  model: {
    prop: "value",
    event: "input",
  },
  props: ["value"],

  methods: {
    onInput(search) {
      this.$emit("input", search);
    },
  },
};
</script>

