<template>
  <v-list subheader>
    <v-subheader>{{ title }}</v-subheader>
    <todo-item
      v-for="task in tasks"
      @switch="switchMarkDone"
      @edit="onEdit"
      @removeTask="onRemoveTask"
      :key="task.id"
      :task="task"
    ></todo-item>
    <v-divider></v-divider>
  </v-list>
</template>

<script>
import TodoItem from "./TodoItem";

export default {
  name: "TodoSection",
  components: {
    TodoItem,
  },
  props: {
    tasks: Array,
    title: String,
  },
  methods: {
    onEdit({ title, id }) {
      this.$emit("edit", { title, id });
    },
    switchMarkDone(id) {
      this.$emit("switch", id);
    },
    onRemoveTask(id) {
      this.$emit("removeTask", id);
    },
  },
};
</script>

<style lang="scss" scoped>
</style>
