<template>
  <v-list-item class="py-3">
    <form class="d-flex flex-grow-1" @submit.prevent="createTask">
      <v-text-field
        v-model="taskTitle"
        label="New task"
        type="text"
        hide-details
        class="mt-0 pt-0"
      ></v-text-field>

      <v-list-item-action class="flex-row">
        <v-btn type="submit" :disabled="!taskTitle" class="primary">
          Add task
        </v-btn>
      </v-list-item-action>
    </form>
  </v-list-item>
</template>

<script>
export default {
  name: "TodoForm",

  data() {
    return {
      taskTitle: "",
    };
  },

  methods: {
    createTask() {
      this.$emit("createTask", this.taskTitle);
      this.taskTitle = "";
    },
  },
};
</script>

<style lang="scss" scoped></style>
