<template>
  <v-card class="mx-auto mt-16" max-width="500">
    <v-toolbar color="primary accent-4" dark>
      <v-toolbar-title>todo list</v-toolbar-title>

      <v-spacer></v-spacer>

      <todo-search @input="onInput" v-model="search" @search="onSearch" />
    </v-toolbar>

    <todo-section
      @switch="switchMarkDone"
      @edit="onEdit"
      @removeTask="onRemoveTask"
      title="todo "
      :tasks="unDoneTasks"
    />

    <todo-section
      @switch="switchMarkDone"
      @edit="onEdit"
      @removeTask="onRemoveTask"
      title="done"
      :tasks="doneTasks"
    />

    <todo-form @createTask="createTask"></todo-form>
  </v-card>
</template>

<script>
// import { mapGetters } from "vuex";
import { mapState } from "vuex";
import TodoForm from "./TodoForm";
import TodoSection from "./TodoSection";
import TodoSearch from "./TodoSearch";
export default {
  name: "todoList",
  components: {
    TodoForm,
    TodoSection,
    TodoSearch,
  },
  data() {
    return {
      search: "",
    };
  },

  computed: {
    // ...mapGetters("tasks", ["doneTasks", "undoneTasks"]),
    ...mapState("tasks", ["tasks"]),
    filteredTasks() {
      if (!this.search) return this.tasks;
      return this.tasks.filter((task) => {
        return (
          task.title.toLowerCase().indexOf(this.search.toLowerCase()) !== -1
        );
      });
    },
    doneTasks() {
      return this.filteredTasks.filter((task) => {
        return task.done;
      });
    },
    unDoneTasks() {
      return this.filteredTasks.filter((task) => {
        return !task.done;
      });
    },
  },

  methods: {
    switchMarkDone(id) {
      this.$store.dispatch("tasks/switchMarkDone", id);
    },
    createTask(title) {
      this.$store.dispatch("tasks/createTask", title);
    },
    onEdit(value) {
      this.$store.dispatch("tasks/edit", value);
    },
    onRemoveTask(id) {
      this.$store.dispatch("tasks/removeTask", id);
    },
    onSearch(title) {
      console.log(title);
    },
    onInput(search) {
      this.search = search;
    },
  },
};
</script>
